
import { defineComponent } from "vue";
import Router from "../router/index";

export default defineComponent({
  name: "Home",

  components: {},
  data() {
    return {
      expanded: false,
      headertitle: [
        "Biography",
        "Current Project",
        "Languages & Tools",
        "Repository View",
        "Contact",
      ],
    };
  },
  methods: {
    expand(i: string) {
      switch (i) {
        case this.headertitle[0]:
          setTimeout(() => {
            Router.push("bio");
          }, 250);
          break;
        case this.headertitle[1]:
          setTimeout(() => {
            Router.push("projects");
          }, 250);
          break;
        case this.headertitle[2]:
          setTimeout(() => {
            Router.push("lang&tools");
          }, 250);
          break;
        case this.headertitle[3]:
          setTimeout(() => {
            Router.push("repoview");
          }, 250);
          break;
        case this.headertitle[4]:
          setTimeout(() => {
            Router.push("contact");
          }, 250);
          break;
      }
    },
  },
});
